<template>
	<section>
		<div class="zx-banner">
      <img src="../../../static/images/zx_banner2.jpg" alt="">
      <div class="zx-sqc" @click="appZl" style="cursor: pointer;">
        <span class="zx-sq-co clearfix"><em></em><p>申请开通专栏</p><em class="zx-sqce"></em></span>
        <span class="zx-sq-ctxt">随心写作，自由表达</span>
      </div>
    </div>
    
    <div class="inner clearfix">
      <ul class="zx-h-dycon clearfix">
        <li v-for="(item,index) in columnList">
        	<!--<a href="javascript:;" class="mask_background">
        		<div class="icon"></div>
        	</a>-->
          <div class="zx-h-dycon-t">
            <img :src="item.columnCover" alt=""   @click="getZL(item.id)" style="cursor: pointer;">
            <p  @click="getZL(item.id)" style="cursor: pointer;height: 24px;">{{item.columnTitle}}</p>
            <span style="height: 50px;" :title="item.columnBrief">{{item.columnBrief | truncate(40)}}</span>
            <em>{{item.subscribeCount||0}}订阅|{{item.articleCount||0}}篇文章</em>
            <a href="javascript:;" v-model="item.subStatus" @click="subscription(item,index)">{{item.subStatus==1?'已订阅':'订阅专栏'}}</a>
            <!--<a href="javascript:;" v-show="item.status==0">已订阅</a>-->
          </div>
          <h1><em>最新更新</em></h1>
          <ul class="zx-h-dycon-lis clearfix" style="height: 70px;">
            <li v-for="(data,index) in item.infoList" @click="getArticle(data.uuid)" v-if="index<3" style="overflow: hidden;"><a href="javascript:;" :title="data.title">{{data.title|truncate(16)}}</a></li>
          </ul>
        </li>  
      </ul>
      <div class="c-page zx-page1">
            <el-pagination
            	:current-page.sync ='currentpage'
       			:page-size="pageSize"
		      	@current-change="pageFn"
			  	layout="prev, pager, next"
			  	:total=total>
			</el-pagination>
	   </div>
    </div>

	</section>
</template>

<script>
	import {Column,dingYue,userLoad,columnMy,information,cancle} from '../../api/info';
	export default{
		data(){
			return{
				pageNum:1,
				pageSize:6,
				currentpage:1,
				columnList:[],
				total:0,
			}
		},
		methods:{
			appZl(){
				var userData = JSON.parse(window.localStorage.getItem('user'))
	    		if(userData){
							
						columnMy({}).then(res=>{
							console.log(res)
							if(res.datas != null){
								// setTimeout(function(){
									
								// },5000)
								this.$message({
									message: '请到我的资讯主页-我的专栏查看申请状态',
									type: 'warning',
									duration:5000
								});
							this.$router.push({path:'/myInfo'})
							
							}else{
								this.$router.push({path:'/application'})
							}
					})
	    			
	    		}else{
	    			window.location.href=configURL.sys+'/login.htm?returnUrl='+configURL.infoUrl+'/page/#/columnList';
	    		} 	
			},
			subscription(item,index){
				var userData = JSON.parse(window.localStorage.getItem('user'))
	    		if(userData){
	    			var param={
						columnId:item.id
					}
					if(item.subStatus==0){
						item.subStatus=1
						item.subscribeCount++
						dingYue(param).then(res=>{
						
						})
					}else if(item.subStatus==1){
						item.subStatus=0
						item.subscribeCount--
						cancle(param).then(res=>{
						
						})
					}
	    		}else{
	    			window.location.href=configURL.sys+'/login.htm?returnUrl='+configURL.infoUrl+'/page/#/columnList';
	    		} 
			},
			getArticle(uuid){
				this.$router.push({path:'/ZLactive',query:{uuid:uuid}})
			},
			getZL(name){
				this.$router.push({path:'/zlDetails',query:{id:name}})
				//window.open()
			},
			ColumnFn(){
				var param={
					pageNum:1,
					pageSize:6,
					
				}
				Column(param).then(res=>{
					
					this.total=res.total;
					this.columnList=res.datas
					
				})
			},
			pageFn(str){
				this.pageNum=str;
				var param={
					pageNum:str,
					pageSize:6,
					
				}
				Column(param).then(res=>{
					this.total=res.total;
					this.columnList=res.datas
					
				})
			}
		},
		mounted(){
			this.ColumnFn();
			this.pageFn();
		}
	}
</script>

<style>
</style>


